/* Bootstrap */

/* Override Bootstrap default variable */
$icon-font-path: 'node_modules/open-iconic/font/fonts/' !default;

@font-face {
  font-family: 'Icons';
  src: url('#{$icon-font-path}open-iconic.eot');
  src: url('#{$icon-font-path}open-iconic.eot?#iconic-sm')
      format('embedded-opentype'),
    url('#{$icon-font-path}open-iconic.woff') format('woff'),
    url('#{$icon-font-path}open-iconic.ttf') format('truetype'),
    url('#{$icon-font-path}open-iconic.svg#iconic-sm') format('svg');
  font-weight: normal;
  font-style: normal;
}

// Catchall baseclass
.oi {
  position: relative;
  top: 1px;
  display: inline-block;
  font-family: 'Icons';
  font-style: normal;
  font-weight: normal;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  &:empty:before {
    width: 1em;
    text-align: center;
    box-sizing: content-box;
  }

  &.oi-align-center:before {
    text-align: center;
  }

  &.oi-align-left:before {
    text-align: left;
  }

  &.oi-align-right:before {
    text-align: right;
  }

  &.oi-flip-horizontal:before {
    -webkit-transform: scale(-1, 1);
    -ms-transform: scale(-1, 1);
    transform: scale(-1, 1);
  }

  &.oi-flip-vertical:before {
    -webkit-transform: scale(1, -1);
    -ms-transform: scale(-1, 1);
    transform: scale(1, -1);
  }

  &.oi-flip-horizontal-vertical:before {
    -webkit-transform: scale(-1, -1);
    -ms-transform: scale(-1, 1);
    transform: scale(-1, -1);
  }
}

